

.cards {
    padding: 5rem;
    padding-top: 20px;
  }
  
  h1 {
    text-align: center;
    
  }
  
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  
  .cards__wrapper {
    position: relative;
    /*margin: 50px 0 45px;*/
  }
  
  .cards__items {
    margin-bottom: 24px;
  }
  
  .cards__item {
    background: #0f0f0f;
    display: flex;
    flex: 1;
    border-radius: 10px;
    margin: 0 1rem;

  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(255, 234, 234, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
   

  .cards__item__img:after {
    background: rgba(0,0,0,0.6);
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: black;
    background-color: whitesmoke;
    box-sizing: border-box;
  }
  
  .cards__item__img{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
    z-index: 0;
  }
  
  .cards__item__img:hover {
    z-index: initial;
    transform: scale(1.1);
  }
  .cards__item__link:hover {
    box-shadow: 0 6px 20px whitesmoke;
  }
  
  
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: var(--primaryTextColor);
    font-size: 18px;
    line-height: 24px;
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }

.pageName {
  font-size: 46px;
  color: rgba(0, 0, 0, .3);
  font-family: Bebas Neue;
}